          </div>
          <div style="height: 45px"></div>
          <div style="display: flex">
            <div style="width: 150px"></div>
            <div style=" width: 250px;
                  height: 750px;
                  background: #ffffff;
                  border-radius: 6px;">
               <el-row
              class="tac"
              style="
                width: auto;
