                    <span
                      type="text"
                      class="arrUp"
                      @click="clickUp"
                      
                    ></span>
                    <span
                      type="text"
                      class="arrDown"
                      @click="clickDown"
                     
                    ></span>
                  </span></el-button-group>

                    
                </div>
               
                 
                

              
              </div>
            </div>
              <div style="height: 20px;"></div>
              <div style="display: flex">
                <div style="width: 55px"></div>
                <div style="width: 100%; height: 600px; flex:1">
                    <el-table
                    :data="tableData.slice(0, 7)"
          style="width: 100%;height: 100%;overflow-y: auto;"
          height="200"
        >
          <el-table-column fixed="left" prop="name" label="项目" width="200">
          </el-table-column>
          <el-table-column fixed prop="reward" label="奖励" width="200">
          </el-table-column>
          <el-table-column fixed prop="qq" label="联系QQ" width="200">
          </el-table-column>
          <el-table-column fixed prop="price" label="价格" width="200">
          </el-table-column>
          
          <el-table-column fixed prop="time" label="时间" width="200">
          </el-table-column>
          <el-table-column fixed prop="info" label="提示信息" width="200">
          </el-table-column>
          
        </el-table>
  
                </div>
              </div>
              <div class="block" style="display: flex; flex-direction: column; margin-top: auto">
                <el-pagination
                layout=" total,prev, pager, next,jumper"
                :total="parseInt(total)"
                :page-size="pagesize"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                @current-change="handlePageChange"
              >
              </el-pagination>
              </div>
            </div>
  
            <div style="width: 110px"></div>
          </div>
         
        </el-main>
      </el-container>
