  </template>
    <script>
  import ajax from "@/untils/axios";
  
  
  import { Message } from 'element-ui'
  export default {
    data() {
    return {
      equipmentId: "",
      tableData: [],
      chaxun: {},
      ad: {},
      prev: 0,
      next: 0,
      id: "",
      input3: "",
      input: "",
      activeName: "first",
      number1: null,
      number2: null,
      num1: null,
      num2: null,
      ishot: 0,
      cost:"",
      latest:0,
      isPopupVisible: false,
      chaui: false,
      addui: false,
      popupPhotoUrl: "", // 添加popupPhotoUrl属性
      currentPage: 1, //初始页
      pagesize: 6, //每页多少数据
      total: "",
    };
  },
    methods: {
      cha(e){  
        const id = parseInt(e);
        ajax.get(`/substitute/${id}`).then((res)=>{
          console.log(res)
          this.chaxun = res.data.data;
          this.chaui=true;
          
        })
  
      },
      
      delet(e) {
        // alert(e);
    const id = parseInt(e);
    ajax.delete(`/substitute/${id}`)
      .then(response => {
        if (response.data.code === 1) {
          console.log("删除成功:", response.data);
          Message.success("删除成功");
          this.chaxun = {};
          location.reload();
        } 
      })
      .catch(error => {
        console.error('删除资源时出错：', error);
        Message.error("删除资源出错")
      });
  },
  
  
  
  
  
      xiugai(){ this.chaui=false;
        const params = {
      name: this.chaxun.name,
      price: this.chaxun.price,
      qq: this.chaxun.qq,
      image:this.chaxun.image,
      weight: this.chaxun.weight,
      reward: this.chaxun.reward,
      info: this.chaxun.info,
      id:this.chaxun.id,
      time:this.chaxun.time,
    };
  
    ajax.post("/substitute",  null, {
      params: params
    })
    .then((response) => {
      if (response.data.code === 1) {
      // 响应中的 code 为 1，表示上传成功
      console.log("修改成功:", response.data);
      Message.success("修改成功");
      this.chaxun={}
      location.reload();
      Message.success("修改成功");
  } else {
      Message.error("修改失败: " + response.data.msg);
  }
  }).catch(error => {
    // 捕获异常
    console.error('请求异常:', error);
    Message.error("修改失败: ")
  });
  },
      quxiao(){
          this.addui=false;
          this.ad=[];
      },
      add() {
    const params = {
      name: this.ad.name,
      price: this.ad.price,
      qq: this.ad.qq,
      image:this.ad.image,
      weight: this.ad.weight,
      reward: this.ad.reward,
      info: this.ad.info,
      time:this.ad.time,
      

    };
  
    ajax.post("/substitute",  null, {
      params: params
    })
    .then((response) => {
      if (response.data.code === 1) {
      // 响应中的 code 为 1，表示上传成功
      console.log("添加成功:", response.data);
      Message.success("添加成功");
      this.ad={}
      location.reload();
      Message.success("添加成功");
  } else {
      Message.error("添加失败: " + response.data.msg);
  }
  }).catch(error => {
    // 捕获异常
    console.error('请求异常:', error);
    Message.error("添加失败");
  });
  },
      // 上传前的处理
      beforeAvatarUpload(file) {
        // 检查文件大小是否超过50MB
        const maxSize = 50 * 1024 * 1024; // 50MB
        if (file.size > maxSize) {
          Message.error('上传的文件大小不能超过50MB');
          return false; // 返回 false 阻止上传
        }
  
        // 检查文件格式是否为jpg或png
        const fileType = file.type;
        if (fileType !== 'image/jpeg' && fileType !== 'image/png') {
          Message.error('上传的文件格式必须为 JPG 或 PNG');
          return false; // 返回 false 阻止上传
        }
  
        // 设置设备ID为文件名
        this.equipmentId = file.name; // 可以根据文件名做适当的处理
  
        // 创建 FormData 对象存储文件和设备ID
        const formData = new FormData();
        // 将文件添加到 FormData
        formData.append('file', file);
        // 将设备ID作为文件名，以txt格式添加到 FormData
        formData.append('equipmentId', `${this.equipmentId}.txt`);
  
        // 使用 ajax 发送 POST 请求
        ajax.post('/uploadFile', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }).then(response => {
          
          
    // 处理响应
    if (response.data.code === 1) {
      // 响应中的 code 为 1，表示上传成功
      this.imageUrl = response.data.data; // 将图片链接保存到 imageUrl 中
      this.ad.image = this.imageUrl; // 将图片链接保存到 ad.image 中
      console.log("上传成功:", response.data);
      Message.success("上传成功");
  } else {
      Message.error("上传失败: " + response.data.msg);
  }
  }).catch(error => {
    // 捕获异常
    console.error('请求异常:', error);
    Message.error("上传失败");
  });
  
  
        // 返回 false 阻止 el-upload 默认的上传行为
        return false;
      },
    
    
    
       
  
      fetchData() {
  ajax.get("/substitute")
    .then((res) => {
      console.log(res);
      this.tableData = res.data.data;
    });
},

clickUp(){
        this.cost=1;
        this.latest=0;
        this.moren()
    },
    clickDown(){
      this.cost=0;
        this.latest=0;
        this.moren()

    },
    daoxu(){
        this.latest=1;
        this.cost=2;
        this.num1='';
        this.num2='';
        this.moren();
    },
   
    handleCalculate() {
      // alert(this.number1),
      // alert(this.number2),
      this.num1 = this.number1;
      this.num2 = this.number2;
     this.moren();
    },
    getHotRanking() {
     this.ishot=1;
     this.moren();
     this.ishot=0;
    },

    search() {
      this.input = this.input3;
      this.moren();
    },
    moren22(){
      this.latest=0;
      this.cost=2;
      this.num1='';
        this.num2='';
      this.moren();
    },
    moren(){
      ajax
        .get("/substitutePage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
            cost:this.cost,
            latest:this.latest,
          },
        })
        .then((res) => {
          console.log(res);
          this.tableData = res.data.data.rows;
        });
    },
    
    handlePageChange(page) {
      this.currentPage = page;
      console.log(this.currentPage);
      ajax
        .get("/substitutePage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
            cost:this.cost,
            latest:this.latest,
          },
        })
        .then((res) => {
          console.log(res);
          this.tableData = res.data.data.rows;
          this.total = res.data.data.total;
        })
        .catch((error) => {
          console.error("获取数据时出错:", error);
        });
    },
    handleSizeChange: function (size) {
      this.pagesize = size;
      console.log(this.pagesize);
    },
    hand1() {
      this.$router.push("/");
    },
    hand2() {
      this.$router.push("/pet01");
    },
    hand3() {
      this.$router.push("/account");
    },
    hand4() {
      this.$router.push("/practise");
    },
    hand5() {
      this.$router.push("/qiu");
    },
    hand6() {
      this.$router.push("/lue");
    },
    hand7() {
      this.$router.push("/tool");
    },
    hand8() {
      this.$router.push("/pianzi");
    },
    hand11() {
      this.$router.push("/");
    },
    hand12() {
      this.$router.push("/proop02");
    },
    hand13() {
      this.$router.push("/proop03");
    },
    hand21() {
      this.$router.push("/pet01");
    },
    hand22() {
      this.$router.push("/pet02");
    },
    hand23() {
      this.$router.push("/pet03");
    },
      showPopup(path) {
              // 点击图片时触发弹窗效果
              this.popupPhotoUrl = path; // 设置弹窗图片路径
              this.isPopupVisible = true; // 显示弹窗
          },
          closePopup() {
              // 关闭弹窗
              this.isPopupVisible = false;
          }
    },
    mounted() {
      this.handlePageChange(this.currentPage);
    },
  };
  import "@/assets/css/head.css";
  import "@/assets/css/imagebag.css";
  </script>
    <style>
     .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }
    .avatar {
      width: 178px;
      height: 178px;
      display: block;
    }
  </style>